<template>
  <!-- 解决方案模块 -->
  <iSection>
    <template slot="title">解决方案</template>
    <template slot="desc">面向行业应用的位置智能技术，将行业和位置结合在一起，<br />激活基于位置的行业分析能力，洞察价值，辅助决策。</template>
    <iRow>
      <iCol v-for="(cate, i) in categories" :key="i" :span="24/categories.length">
        <SectionItem>
          <img :src="cate.imgUrl" slot="img" /> 
          {{cate.desc}}
        </SectionItem>
      </iCol>
    </iRow>
  </iSection>
</template>

<script>
  import {Row, Col} from 'iview/src/components/grid'
  import {homeImgDir} from '@/utils/staticUrl'

  export default {
    name: 'homepage_third_section',
    components: {
      iRow: Row,
      iCol: Col,
      iSection: () => import(/* webpackChunkName: "common" */ './Section'),
      SectionItem: () => import(/* webpackChunkName: "homepage" */ './ThirdSectionItem')
    },
    data () {
      return {
        categories: Array(6).fill(0).map(() => ({
          imgUrl: `${homeImgDir}/solution_1.png`,
          desc: '政府'
        }))
      }
    }
  }
</script>

<style>
  
</style>


