<template>
  <!-- 分享模块展示项 -->
  <div :class="classes">
    <slot></slot>
  </div>
</template>

<script>
  const prefixCls = 'gtu-index-sharing'

  export default {
    name: 'homepage_second_section_item',
    data () {
      return {}
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-index-sharing';
  $itemPadding: 10px;

  #{$prefixCls} {
    padding: $itemPadding;
    img {
      display: block;
    }
  }

</style>


