<template>
  <!-- 声音 -->
  <iSection>
    <template slot="title">声音</template>
    <template slot="desc">“很高兴集思图友一直保持着顶尖的设计水平，让我们更好的体验地图之美！” <br />加拿大＊＊＊可视化CTO－－啊博尔特·又因斯坦</template>
    <div :class="innerWrapClasses">
      <SectionItem v-for="(star, i) in starLists" :key="i" :style="itemStyles">
        <img :src="star.imgUrl" />
      </SectionItem>
    </div>
  </iSection>
</template>

<script>
  import {homeImgDir} from '@/utils/staticUrl'
  const prefixCls = 'gtu-index-section4'

  export default {
    name: 'homepage_fourth_section',
    components: {
      iSection: () => import(/* webpackChunkName: "homepage" */ './Section'),
      SectionItem: () => import(/* webpackChunkName: "homepage" */ './FourthSectionItem')
    },
    data () {
      return {
        starLists: Array(5).fill(0).map(() => ({
          imgUrl: `${homeImgDir}/voice_1.png`
        }))
      }
    },
    computed: {
      innerWrapClasses () {
        return [`${prefixCls}-inner-wrap`]
      },
      itemStyles () {
        let width = 100 / this.starLists
        return {
          width: `${width}%`
        }
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-index-section4';
  $imgWidth: 150px;
  $imgHeight: 150px;

  #{$prefixCls} {
    &-inner-wrap {
      width: ($imgWidth + 10) * 5;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-around;
    }
  }
</style>


