<template>
  <!-- 解决方案：展示项 -->
  <div :class="classes">
    <dl>
      <dt :class="imgClasses"><slot name="img"></slot></dt>
      <dd :class="descClasses"><slot></slot></dd>
    </dl>
  </div>
</template>

<script>
  const prefixCls = 'gtu-index-solution'

  export default {
    name: 'homepage_third_section_item',
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      imgClasses () {
        return [`${prefixCls}-img`]
      },
      descClasses () {
        return [`${prefixCls}-desc`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : ".gtu-index-solution";
  $itemPadding : 5px;

  #{$prefixCls} {
    padding: $itemPadding;
    &-img {
      margin-bottom:  10px;
      img {
        max-width: 100%;
        display: block;
      }
    }
    &-desc {
      font: {
        size: 24px;
        weight: 300;
      }
      text-align: center;
    }
  }
</style>


