<template>
  <!-- 分享模块 -->
  <iSection>
    <template slot="title">分享</template>
    <template slot="desc">让世界体验你的精彩</template>
    <div :class="innerWrapClasses">
      <iRow>
        <iCol v-for="(sharingCase, i) in sharingCases" :span="24/4" :key="i">
          <SectionItem>
            <img :src="sharingCase.imgUrl"  />
          </SectionItem>
        </iCol>
      </iRow>
    </div>
  </iSection>
</template>

<script>
  import {Row, Col} from 'iview/src/components/grid'
  import {homeImgDir} from '@/utils/staticUrl'

  const prefixCls = 'gtu-index-section2'
  export default {
    name: 'homepage_second_section',
    components: {
      iRow: Row,
      iCol: Col,
      iSection: () => import(/* webpackChunkName: "common" */ './Section'),
      SectionItem: () => import(/* webpackChunkName: "homepage" */ './SecondSectionItem')
    },
    data () {
      return {
        sharingCases: Array(9).fill(0).map(() => (
          {
            imgUrl: `${homeImgDir}/share_1.png`,
            linkUrl: '/'
          }
        ))
      }
    },
    computed: {
      innerWrapClasses () {
        return [`${prefixCls}-inner-wrap`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : ".gtu-index-section2";
  $imgWidth: 200px;
  $imgHeight: 150px;
  $innerWrapWidth : ($imgWidth + 20px) * 4;

  #{$prefixCls} {
    &-inner-wrap {
      width: $innerWrapWidth;
      margin: 0 auto;
    }
  }

</style>


