<template>
  <!-- 声音：展示项 -->
  <div :class="classes">
    <slot></slot>
  </div>
</template>

<script>
  const prefixCls = 'gtu-index-voice'

  export default {
    name: 'homepage_fourth_section_item',
    computed: {
      classes () {
        return [`${prefixCls}`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : '.gtu-index-voice';
  $itemPadding: 5px;
  $imgWidth: 150px;

  #{$prefixCls} {
    padding: $itemPadding;
    img {
      display: block;
      width: $imgWidth;
    }
  }
</style>


