<template>
  <!-- 主页 -->
  <div :class="classes">
    <!-- BEGIN:banner -->
    <Banner :bg="bannerBg">
      <template slot="header">把地理价值带到您的身边</template>
      <template>GIS TO YOU <br /> 集思广益 以图会友</template>
    </Banner>
    <!-- END: banner -->
    <!-- BEGIN: 平台功能 -->
    <FirstSection></FirstSection>
    <!-- END: 平台功能 --> 
    <!-- BEGIN: 分享 -->
    <SecondSection></SecondSection>
    <!-- END: 分享 -->
    <!-- BEGIN: 解决方案 -->
    <ThirdSection></ThirdSection>
    <!-- END: 解决方案 -->
    <!-- BEGIN: 声音 -->
    <FourthSection></FourthSection> 
    <!-- END: 声音 -->
  </div>
</template>

<script>
  import {imgDir} from '@/utils/staticUrl'
  const prefixCls = 'gtu-index'

  export default {
    name: 'homapage',
    components: {
      Banner: () => import(/* webpackChunkName: "common" */ '@/components/banner'),
      FirstSection: () => import(/* webpackChunkName: "homepage" */ './FirstSection'),
      SecondSection: () => import(/* webpackChunkName: "homepage" */ './SecondSection'),
      ThirdSection: () => import(/* webpackChunkName: "homepage" */ './ThirdSection'),
      FourthSection: () => import(/* webpackChunkName: "homepage" */ './FourthSection')
    },
    porps: {},
    data () {
      return {
        bannerBg: `${imgDir}/home_banner.jpg`
      }
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      }
    }
  }
</script>



