<template>
  <!-- 平台功能模块：展示项 -->
  <div :class="classes">
    <dl>
      <dt>
        <div :class="imgClasses"><slot name="img"></slot></div>
      </dt>
      <dd>
        <div :class="titleClasses"><slot name="title"></slot></div>
        <p :class="descClasses"><slot name="desc"></slot></p>
      </dd>
    </dl>
  </div>
</template>

<script>
  const prefixCls = 'gtu-index-intro'

  export default {
    name: 'homepage_first_section_item',
    data () {
      return {}
    },
    computed: {
      classes () {
        return [`${prefixCls}`]
      },
      imgClasses () {
        return [`${prefixCls}-img`]
      },
      titleClasses () {
        return [`${prefixCls}-title`]
      },
      descClasses () {
        return [`${prefixCls}-desc`]
      }
    }
  }
</script>

<style lang="scss">
  $prefixCls : ".gtu-index-intro";
  $imgWidth: 110px;
  $imgHeight: 100px;
  $defaultGap: 20px;

  #{$prefixCls} {
    padding: $defaultGap;
    &-img {
      width: $imgWidth;
      height: $imgHeight;
      font-size: 0;
      margin: 0 auto;
      img {
        max-width: 100%;
      }
    }
    &-title {
      font: {
        size: 24px;
        weight: 300;
      }
      margin: $defaultGap 0;
      text-align: center;
    }
    &-desc {
      line-height: 2;
    }
  }
</style>


